import Task from "../components/Task"
  const Tasks=(props)=>{
       return (
        <div>
            {props.tasks.map((task)=>{
                return(
                    <div>
                      
                       <Task  task1={task.id} tasks={props.tasks} key={task.id} text={task.text} day={task.day} onDelete={props.onDelete}  />
                </div>
                )
              
            })}
        </div>
    )
}



export default Tasks
