import {FaTimes} from 'react-icons/fa'
function Task(props) {
    return (
        <div  className='task' >
              
            <h3>{props.text} <FaTimes  style={{color:'red',cursor:'pointer'}}onClick={()=>props.onDelete(props.task1)}/></h3>
            <p>{props.day}</p>
        </div>
    )
}

export default Task
